"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { BarChart3, BookOpen, LayoutDashboard, MessageSquare, Settings, Users, Video } from "lucide-react"

import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"

export function InstructorSidebar({ className }: { className?: string }) {
    // Using query param or state for tab selection if we stay on single page, 
    // but for a real dashboard sidebar usually assumes routing.
    // Since we are inside a Tabs component in the unified page, this sidebar 
    // might be visual representation or actually control the tabs if we refactor.
    // For now, let's make it styled correctly.

    return (
        <div className={cn("pb-12 h-full border-r bg-card", className)}>
            <div className="space-y-4 py-4">
                <div className="px-3 py-2">
                    <h2 className="mb-2 px-4 text-lg font-semibold tracking-tight">
                        Instructor
                    </h2>
                    <div className="space-y-1">
                        <Button variant="secondary" className="w-full justify-start">
                            <LayoutDashboard className="mr-2 h-4 w-4" />
                            Dashboard
                        </Button>
                        <Button variant="ghost" className="w-full justify-start">
                            <BookOpen className="mr-2 h-4 w-4" />
                            Courses
                        </Button>
                        <Button variant="ghost" className="w-full justify-start">
                            <MessageSquare className="mr-2 h-4 w-4" />
                            Communication
                        </Button>
                        <Button variant="ghost" className="w-full justify-start">
                            <BarChart3 className="mr-2 h-4 w-4" />
                            Performance
                        </Button>
                        <Button variant="ghost" className="w-full justify-start">
                            <Settings className="mr-2 h-4 w-4" />
                            Tools
                        </Button>
                        <Button variant="ghost" className="w-full justify-start">
                            <Users className="mr-2 h-4 w-4" />
                            Resources
                        </Button>
                    </div>
                </div>
            </div>
        </div>
    )
}
